@font-face {
  font-family: silkaSemiBold;
  src: url(../public/assets/fonts/silka-regular-webfont.ttf);
  font-weight: semi-bold;
}

@font-face {
  font-family: avenirNext;
  src: url(../public/assets/fonts/avenir-next-regular.ttf);
}

/* General text */

body {
  font-family: 'avenirNext', sans-serif;
}

h1 {
  font-family: 'silkaSemiBold', sans-serif;
  margin-bottom: 0;
}

h2 {
  font-family: 'silkaSemiBold', sans-serif;
}

.page-header p {
  color: #ffffff;
  font-size: 3em;
  font-weight: 600;
  text-align: center;
}

.form-cta label {
  font-size: 1.2em;
}

/* Form and input fields */

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

form label {
  margin-bottom: 1em;  
}

form input {
  margin-bottom: 1em;  
}

form input {
  padding: 10px 16px;
  border-radius: 4px;
  border: 1px solid;
}

/* Sections */

.topNavigationBar {
  padding: 1.5em;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.page-header {
  background-image: url('assets/images/Cat_mosaique.png');
  background-repeat: repeat;
  height: 400px;
  padding: 1em 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

section {
  background-color: #E5E5E5;
}

.form-cta {
  padding: 4em 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.twoButtons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.intro-text {
  padding: 0 25% 2em 25%;
  display: flex;
  flex-direction: column;
}

.columns-211 {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 2fr 1fr 1fr;
  grid-template-areas: 
      "col1 col2 col3";
  gap: 2em;
  padding: 0 2em;
}

.columns-211-col1 {
  grid-area: col1;
}

.columns-211-col2 {
  grid-area: col2;
}

.columns-211-col3 {
  grid-area: col3;
}

.columns-211-col1-img {
  width: 100%;
}

.teaser {
  display: flex;
  flex-direction: column;
}

.teaser-img {
  width: 100%;
}

footer {
  background-color: #E5E5E5;
  height: 2em;
}

/* Buttons */

button {
  font-family: 'silkaSemiBold', sans-serif;
  font-weight: 600;
  padding: 12px 24px;
  border-radius: 50px;
  border: 2px solid;
  text-transform: uppercase;
  margin: 1em;
}

.button-teal-solid {
  background-color: #23B8CC;
  border-color: #23B8CC;
  color: #ffffff;
}

.button-teal-solid:hover {
  background-color: transparent;
  color: #23B8CC;
}

.button-teal-outline {
  background-color: transparent;
  border-color: #23B8CC;
  color: #23B8CC;
}

.button-teal-outline:hover {
  background-color: #23B8CC;
  color: #ffffff;
}

@media screen and (max-width: 60em) {
  .page-header {
    height: 250px;
  }

  .page-header p {
    font-size: 2em;
    font-weight: 600;
  } 
 
  .form-cta {
    padding: 4em 2em 0;
  }

  .intro-text {
      padding: 2em;
  }

  .columns-211 {
      display: grid;
      grid-template-rows: repeat(3, auto);
      grid-template-columns: auto;
      grid-template-areas: 
          "col1"
          "col2"
          "col3";
      gap: 1em;
      padding: 0 1em;
  }

  .columns-211-col1 {
    grid-area: col1;
  }
  
  .columns-211-col2 {
    grid-area: col2;
  }
  
  .columns-211-col3 {
    grid-area: col3;
  }

  .teaser {
      flex-direction: row;
      align-items: flex-start;
  }

  .teaser-img {
    width: 50%;
    margin-right: 1em;
  }

  .teaser h2 {
    font-size: 100%;
  }
      
}